













import Vue from 'vue';
import { mapState } from 'vuex';
import { AboutState } from '@/store/modules/about';
import { RootState } from '@/store';
import WindowFrame from '@/components/layout/WindowFrame.vue';

export default Vue.extend({
    computed: mapState<RootState>({
        msg: (state: { about: AboutState }) => state.about.msg,
        timestamp: (state: { about: AboutState }) => state.about.timestamp,
    }),
    components: {
        WindowFrame,
    },
});
